import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/loadmore', meta: { name: '' }, component: () => import('../routers/loadmore.vue') },
    { path: '/citySelector', meta: { name: '' }, component: () => import('../routers/citySelector.vue') },
    { path: '/noticeBar', meta: { name: '' }, component: () => import('../routers/noticeBar.vue') },
    { path: '/childinsuranceCell', meta: { name: '' }, component: () => import('../routers/childinsuranceCell.vue') },
    { path: '/swiper', meta: { name: '' }, component: () => import('../routers/swiper.vue') },
    { path: '/popup', meta: { name: '' }, component: () => import('../routers/popup.vue') },
    { path: '/picker', meta: { name: '' }, component: () => import('../routers/picker.vue') },
    { path: '/agree', meta: { name: '' }, component: () => import('../routers/agree.vue') },
    { path: '/actionsheet', meta: { name: '' }, component: () => import('../routers/actionsheet.vue') },
    { path: '/dialog', meta: { name: '' }, component: () => import('../routers/dialog.vue') },
    { path: '/toast', meta: { name: '' }, component: () => import('../routers/toast.vue') },
    { path: '/radio', meta: { name: '' }, component: () => import('../routers/radio.vue') },
    { path: '/field', meta: { name: '' }, component: () => import('../routers/field.vue') },
    { path: '/cell', meta: { name: 'cell' }, component: () => import('../routers/cell.vue') },
    { path: '/tabs', meta: { name: '' }, component: () => import('../routers/tabs.vue') },
    { path: '/carlicense', meta: { name: '' }, component: () => import('../routers/carlicense.vue') },
    { path: '/button', meta: { name: '' }, component: () => import('../routers/button.vue') },
    { path: '/index', meta: { name: '' }, component: () => import('../routers/index.vue') },
    { path: '/', redirect: '/index' }
  ]
})



